<template>
  <div class="register-component">
    <h1 class="form-title">{{ $t('login.new_account_registration') }}</h1>

    <form-register
      @validate="$emit('validate-registration-success', $event)"
      ref="formRegister" />

    <div class="register-bottom-part">
      <license-agreement @change="submitDisableStatus = $event" />

      <el-button
        type="primary"
        class="register-submit"
        :disabled="!submitDisableStatus"
        @click="handleSubmit">{{ $t('login.confirm_transaction') }}</el-button>

      <div class="flex-space-between">
        <back-login :text="$t('login.already_account')" />
        <p
          @click="$emit('navigate-backward', 'PageVerifyCode')"
          class="jump-back">{{ $t('login.modify_email') }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import BackLogin from '../../components/BackLogin';
import LicenseAgreement from './LicenseAgreement';
import FormRegister from './FormRegister';

export default {
  components: {
    LicenseAgreement,
    FormRegister,
    BackLogin,
  },
  data() {
    return {
      submitDisableStatus: false,
    };
  },
  methods: {
    handleSubmit() {
      this.$refs.formRegister.validateForm();
    },
  },
};
</script>

