<template>
  <div class="verify-email">
    <h1 class="verify-email-title">{{ $t('login.email_verification') }}</h1>

    <FormEmailVerifyCodeByStep
      mode="dynamicVerifyEmail"
      @validate="submit"
      ref="formEmailVerifyCode" />

    <!-- <el-button
      class="verify-email-submit"
      type="primary"
      size="medium"
      @click="handleNext">{{ $t('public.form.next') }}</el-button> -->

    <back-login class="back-login-wrap" :text="$t('login.already_account')" />
  </div>
</template>

<script>
import FormEmailVerifyCodeByStep from '@/components/form/whole-form/FormEmailVerifyCodeByStep';

import { checkVerificationCodeMatching } from '@/api/email';
import mixinLoading from '@/mixins/loading';
import BackLogin from '../../components/BackLogin';

export default {
  components: {
    FormEmailVerifyCodeByStep,
    BackLogin,
  },
  mixins: [mixinLoading],
  methods: {
    async submit(form) {
      this.startLoading(); // 打开Loading状态
      try {
        await checkVerificationCodeMatching({ ...form, type: 1 });
        this.$emit('verify-email-success', form);
      } catch (err) {
        this.$errHandler(err);
      } finally {
        this.endLoading(); // 关闭Loading状态
      }
    },
    // handleNext() {
    //   this.$refs.formEmailVerifyCode.validateForm();
    // },
  },
};
</script>

