<template>
  <div class="register-page">
    <img
      v-if="currentComponent !== 'PageSuccessMessage'"
      :src="logo"
      class="logo"
      :class="{'logo-anta':appName === 'anta'}"
      :alt="$t('login.index.logo_alt')"
      @click="$router.push({ name: 'Login' })" />
    <component
      @verify-email-success="handleVerfyEmailSuccess"
      @validate-registration-success="handleRegisterAccount"
      @navigate-backward="currentComponent = $event"
      :is="currentComponent" />
    <BusinessFootNote v-if="appName === 'anta'"/>
  </div>
</template>

<script>
import mixinLoading from '@/mixins/loading';
import { registerAccount } from '@/api/account';
import envs from '../../../utils/envs';
import BusinessFootNote from '../components/BusinessFootNote.vue';
import PageVerifyCode from './components/PageVerifyCode';
import PageRegisterAccount from './components/PageRegisterAccount';
import PageSuccessMessage from './components/PageSuccessMessage';


export default {
  components: {
    PageVerifyCode,
    PageRegisterAccount,
    PageSuccessMessage,
    BusinessFootNote,
  },
  mixins: [mixinLoading],
  data() {
    return {
      currentComponent: 'PageVerifyCode',
      form: {},
    };
  },
  computed: {
    logo() {
      return envs.VUE_APP_COOPERATION === 'anta' ? require('@/assets/img/login/logo-anta.png')
        : require('@/assets/img/login/logo-register.png');
    },
    appName() {
      return envs.VUE_APP_COOPERATION;
    },
  },
  methods: {
    async handleRegisterAccount(obj) {
      this.startLoading(); // 打开Loading状态
      Object.assign(this.form, obj);
      delete this.form.passwordConfirm;

      try {
        await registerAccount(this.form);
        this.currentComponent = 'PageSuccessMessage'; // 显示完成界面
      } catch (e) {
        this.$errHandler(e);
      } finally {
        this.endLoading(); // 关闭Loading状态
      }
    },
    // 校验邮箱验证通过后，调整到注册详情页面；
    handleVerfyEmailSuccess(mailForm) {
      Object.assign(this.form, { mail: mailForm.email.toLowerCase(), verifyCode: mailForm.verifyCode });
      this.currentComponent = 'PageRegisterAccount';
    },
  },
};
</script>

<style lang="scss" scoped>
.register-page {
  position: relative;
  padding: 0 40px 60px;
  min-height: 480px;
  min-width: 400px;

  .logo {
    margin: 60px 0 40px -16px;
    height: 24px;
    cursor: pointer;
    &.logo-anta {
      height: 19px;
      margin: 64px 0 40px 0;
    }
  }
}
</style>
